import {request} from "./request"
//1.提交反馈意见表单信息
export function addfeedbackData(data){
  return request({
    url:'/main/feedbackInfo/feedback',
    method:'post',
    data:data,
    headers:{'X-Content-Type': 'multipart/form-data'}
  })
}
